define(["require", "exports", 'client/api/story/story.api', 'client/actions/location/location.actions', 'client/actions/story/story.actions', 'react-router-redux'], function (require, exports, story_api_1, location_actions_1, story_actions_1, react_router_redux_1) {
    "use strict";
    function configureKernel(store) {
        var storyApi = new story_api_1.StoryApi();
        var locationActions = new location_actions_1.LocationActions(store.getState, react_router_redux_1.routerActions);
        var storyActions = new story_actions_1.StoryActions(store.getState, storyApi);
        return {
            store: store,
            actions: {
                locationActions: locationActions,
                storyActions: storyActions
            }
        };
    }
    exports.configureKernel = configureKernel;
});



