define(["require", "exports", 'rx', 'client/app/index'], function (require, exports, rx_1, index_1) {
    "use strict";
    function registerActionCreators() {
        var observables = [];
        for (var key in index_1.kernel.actions) {
            observables.push(index_1.kernel.actions[key].getObservable());
        }
        rx_1.Observable.merge(observables)
            .subscribe(function (action) {
            index_1.kernel.store.dispatch(action);
        });
    }
    exports.registerActionCreators = registerActionCreators;
});



