define(["require", "exports", 'react-tap-event-plugin'], function (require, exports, injectTapEventPlugin) {
    "use strict";
    function configureMaterialUi() {
        injectTapEventPlugin();
    }
    exports.configureMaterialUi = configureMaterialUi;
});



